import { Component, h, Prop, Host, State, Watch } from '@stencil/core';

@Component({
  tag: 'ks-cart-message',
  styleUrl: 'cartMessage.css'
})
export class CartMessage {
    @Prop() message: string = "";

    @State() visibility: string = "hide";
    @State() messageDelay: string = "";

    componentWillLoad() {
        this.watchHandler(this.message);
    }

    @Watch('message')
    watchHandler(newValue: string) {
        if(newValue == "") {
            this.visibility = "hide";
            setTimeout(() => {
                this.messageDelay = "";
            }, 500);
        }
        else {
            this.visibility = "show";
            this.messageDelay = this.message;
        }
    }

    render() {
        return(
            <Host class={this.visibility}>
                <p>{this.messageDelay}</p>
            </Host>
        );
    }
}